html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
}

body {
  font-size: calc(1rem + 0.75vw);
  margin: 0;
  padding: 0;
  font-family: "Bai Jamjuree", sans-serif;
  color: #9fabb2;
  line-height: 1.4;
}

h1, h2, h3 {
  margin-top: 0;
  text-align: center;
  color: #4c545d;
}

.h1--large {
  font-size: 1.6em;
}
.h1--small {
  font-size: 1em;
  margin-bottom: 10px;
}

a, a:visited, a:active {
  text-decoration: none;
}

p {
  text-align: center;
  color: #9fabb2;
  line-height: 1.5;
}

.p--large {
  font-size: 0.85em;
}
.p--small {
  font-size: 1rem;
}

.center {
  text-align: center;
}

.justify {
  text-align: justify;
}

@media (min-width: 40rem) {
  .box--align p {
    text-align: justify;
  }
}
@media (min-width: 40rem) {
  .box--align h1 {
    text-align: justify;
  }
}

section {
  max-width: 1440px;
  margin: 0 auto;
}

.container {
  display: flex;
  flex-direction: column;
  padding: 0 30px;
  margin: 0 auto;
  max-width: 690px;
  align-items: center;
}
@media (min-width: 64rem) {
  .container {
    padding: 0 0;
  }
}

.buttom {
  font-size: 1rem;
  color: #ffffff;
  width: 309px;
  justify-self: center;
  padding: 20px 75px;
  margin-bottom: 25px;
  border-radius: 40px;
  text-align: center;
}
@media (min-width: 40rem) {
  .buttom {
    padding: 17px 35px;
    width: 225px;
  }
}
.buttom-cyan {
  background-color: #26baa4;
  box-shadow: 0px 5px 10px rgba(38, 186, 164, 0.4);
  transition: 250ms ease-in-out;
}
.buttom-cyan:hover {
  background-color: rgba(38, 186, 164, 0.8);
}
.buttom-blue {
  background-color: #6173ff;
  box-shadow: 0px 5px 10px rgba(97, 115, 255, 0.4);
  transition: 250ms ease-in-out;
}
.buttom-blue:hover {
  background-color: rgba(97, 115, 255, 0.8);
}

.header {
  background: url("../../../images/bg-header-mobile.png");
  background-repeat: no-repeat;
  background-size: cover;
  max-width: 1440px;
  margin: 0 auto;
  height: 20.9375rem;
  display: flex;
  justify-content: center;
}
@media (min-width: 40rem) {
  .header {
    background: url("../../../images/bg-header-desktop.png");
    height: 340px;
    background-position: center;
  }
}
.header-logo {
  margin-top: 125px;
}

.history {
  margin-bottom: 130px;
}
.history h1 {
  margin-bottom: 22px;
}
.history p {
  margin-bottom: 50px;
}
.history-buttons {
  display: flex;
  flex-direction: column;
}
@media (min-width: 40rem) {
  .history-buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 15px;
  }
}

.snippets {
  margin-bottom: 130px;
}
.snippets__container {
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  row-gap: 50px;
  column-gap: 50px;
}
@media (max-width: 64rem) {
  .snippets__container {
    padding: 0 30px;
  }
}
@media (min-width: 64rem) {
  .snippets__container {
    grid-template-rows: 1fr 100px 100px 100px;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 130px;
  }
}
.snippets__container img {
  width: 100%;
  max-width: 752px;
}
.snippets__track {
  max-width: 690px;
}
@media (min-width: 40rem) {
  .snippets__track {
    grid-row: 1/2;
    grid-column: 1/3;
    max-width: 690px;
    margin: 0 auto;
  }
}
.snippets__track h1 {
  margin-bottom: 22px;
}
.snippets__image {
  max-width: 600px;
}
@media (min-width: 40rem) {
  .snippets__image {
    grid-row: 2/5;
    grid-column: 1/2;
    max-width: 100%;
    justify-self: start;
    align-self: center;
  }
}
@media (min-width: 64rem) {
  .snippets__image {
    align-self: start;
    transform: translateX(-50px);
  }
}
.snippets .box {
  max-width: 350px;
}
@media (min-width: 64rem) {
  .snippets .box {
    transform: translateY(50px);
    justify-self: start;
  }
}
@media (min-width: 40rem) {
  .snippets__search {
    grid-row: 2/3;
    grid-column: 2/3;
  }
}
@media (min-width: 40rem) {
  .snippets__sync {
    grid-row: 3/4;
    grid-column: 2/3;
  }
}
@media (min-width: 40rem) {
  .snippets__history {
    grid-row: 4/5;
    grid-column: 2/3;
  }
}

.clipboard {
  margin-bottom: 50px;
}
@media (min-width: 64rem) {
  .clipboard {
    margin-bottom: 100px;
  }
}
.clipboard__container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 30px;
}
.clipboard__container p {
  margin-bottom: 70px;
}
@media (min-width: 64rem) {
  .clipboard__container p {
    margin-bottom: 100px;
  }
}
.clipboard__container h1 {
  margin-bottom: 20px;
}
.clipboard__image img {
  width: 100%;
  max-width: 800px;
}

.workflow .container {
  margin-bottom: 80px;
}
.workflow .container h1 {
  margin-bottom: 20px;
}
.workflow__features {
  display: grid;
  grid-template-columns: 1fr;
  column-gap: 30px;
  row-gap: 50px;
  padding: 0 30px;
  max-width: 1100px;
  margin: 0 auto 130px;
}
@media (min-width: 40rem) {
  .workflow__features {
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 150px;
  }
}
.workflow__features .feature {
  max-width: 350px;
  margin: 0 auto;
}
.workflow__features .feature__image {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}
.workflow__features .feature__image img {
  height: 35px;
}
.workflow__features .feature__title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #4c545d;
  text-align: center;
  margin-bottom: 30px;
}
.workflow__features .feature__text {
  font-size: 1rem;
  text-align: center;
}